
.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  text-align: center;
}


a {
  color: whitesmoke;
  cursor: pointer;
}

fieldset {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px 0;
  border: 0;
}

fieldset input {
  margin: 0 auto;
}
