@import url(https://fonts.googleapis.com/css?family=Lato:400,700);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-size: 10px;
  list-style: none;
}


body {
  margin: 0;
  padding: 100px 0;
  color: rgb(126, 154, 186);
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #5482dd;
}

button {
  background-color: rgb(56, 184, 98);
  font-size: 1.4rem;
  border-radius: 2px;
  border: none;
  color: whitesmoke;
  font-weight: bold;
  padding: 10px 20px;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  text-align: center;
}


a {
  color: whitesmoke;
  cursor: pointer;
}

fieldset {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px 0;
  border: 0;
}

fieldset input {
  margin: 0 auto;
}

.flex {
	display: flex;
}

.flex-column {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-end;
}

nav {
	display: flex;
	justify-content: center;
	position: fixed;
	width: 100vw;
	top: 0;
	left: 0;
	padding: 10px 0;
	background-color: rgba(255, 255, 255, 0.8);
	box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.4);
	z-index: 1;
}


.link {
	text-align: right;
	text-decoration: none;
	color: rgb(52, 52, 136);
	font-weight: bold;
	font-size: 2rem;
	text-shadow: 0 0 2px 0 black;
}

.header img {
	width: 30px;
	height: auto;
	margin-right: 10px;
}

.nav-box {
	width: 200px;
	height: 80px;
	align-content: flex-end;
	justify-content: flex-end;
	background-color: tomato;
	background-size: cover;
	background-position-y: -45px;
	background-position-x: -30px;
	margin: 0 5px;
	padding: 0 10px;
	color: whitesmoke;
	font-size: 20px;
	-webkit-text-fill-color: white;
  -webkit-text-stroke-width: 0.5px;
	-webkit-text-stroke-color: black;
	-webkit-transition: -webkit-transform .2s;
	transition: -webkit-transform .2s;
	transition: transform .2s;
	transition: transform .2s, -webkit-transform .2s;
}

.nav-box:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.nav-style .home {
	background-color: rgb(101, 101, 207);
	background-image: url('/images/navbar/home.png');
}

.nav-style .shop {
	background-position-y: -49px;
	background-color: rgb(44, 182, 101);
	background-image: url('/images/navbar/shop.png');
}

.nav-style .market {
	background-position-y: -48px;
	background-color: rgb(140, 63, 175);
	background-image: url('/images/navbar/market.png');
}

.nav-style .aboutus {
	background-color: rgb(220, 230, 90);
	background-image: url('/images/navbar/aboutus.png');
}

.nav-style .user {
	background-image: url('/images/navbar/user.png');
}

.nav-style .signup {
	background-color: rgb(220, 230, 90);
	background-image: url('/images/navbar/signup.png');
}

.nav-style .login {
	background-color: rgb(90, 179, 230);
	background-image: url('/images/navbar/login.png');
}


.searchBar {
	margin: 10px;
	margin-right: 70px;
	margin-bottom: 0;
}

.searchBar input {
	width: 60vw;
	padding: 10px 20px;
	background-color: #19202e;
	border: none;
	color: white;
	font-size: 1.6rem;
}

.searchBar input::-webkit-input-placeholder {
	color: rgba(255, 255, 255, 0.4);
	font-size: 1.6rem;
}

.searchBar input::-moz-placeholder {
	color: rgba(255, 255, 255, 0.4);
	font-size: 1.6rem;
}

.searchBar input::-ms-input-placeholder {
	color: rgba(255, 255, 255, 0.4);
	font-size: 1.6rem;
}

.searchBar input::placeholder {
	color: rgba(255, 255, 255, 0.4);
	font-size: 1.6rem;
}
.container-card {
  position: relative;
  margin: auto;
  margin-top: 50px;
  overflow: hidden;
  width: 70vw;
  height: 70vh;
  background: #F5F5F5;
  box-shadow: 5px 5px 15px rgba(126, 154, 186, .5);
  border-radius: 10px;
  color: rgb(126, 154, 186);
}

.container-card h3 {
  color: rgb(126, 154, 186);
}

.signup-form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.signup-form input {
  margin: 10px;
}
.main h1 {
  font-size: 3.5rem;
}

.main h3 {
  font-size: 2.2rem;
}
.containerCard {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.containerCard img {
  max-width: 100%;
  max-height: 100%;
}

.containerCard img:hover {
  max-width: 100%;
  max-height: 100%;
  -webkit-filter: brightness(20%);
          filter: brightness(20%);
}

.gameCard {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: auto;
  height: 250px;
  margin: 1vw;
  background: linear-gradient(
    344deg,
    rgba(83, 28, 119, 0.644) 31%,
    rgba(232, 137, 235, 0.493) 100%
  );
  box-shadow: 2px 0px 10px rgba(0, 0, 0, 0.363);
}

.linked {
  text-decoration: none;
}

.description-section {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 70%;
  top: -125px;
}

.gameCard h1 {
  text-align: center;
  font-size: 2rem;
  margin: auto 0;
}






.gameList {
  justify-content: flex-start;
  align-items: flex-start;
  width: 80vw;
}

.container {
  margin: 40px;
  left: calc(50% - 300px);
  display: flex;

}

.card {
  display: flex;
  height: 250px;
  width: auto;
  background-color: #17141d;
  border-radius: 10px;
  box-shadow: -1rem 0 3rem #000;
  margin-left: -50px;
  -webkit-transition: 0.4s ease-out;
  transition: 0.4s ease-out;
  position: relative;
  left: 0px;
  -webkit-filter: brightness(100%);
          filter: brightness(100%);

}

.card:not(:first-child) {
    margin-left: -50px;
}

.card:hover {
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  -webkit-transition: 0.4s ease-out;
  transition: 0.4s ease-out;
  -webkit-filter: brightness(20%);
          filter: brightness(20%);
}

.card:hover ~ .card {
  position: relative;
  left: 50px;
  -webkit-transition: 0.4s ease-out;
  transition: 0.4s ease-out;
}

.container img {
  border-radius: 10px;
  max-width: 100%;
  max-height: 100%;
}

.text-img {
  display: flex;
  height: 250px;
  width: 90px;
  margin-left: -50px;
  -webkit-transition: 0.4s ease-out;
  transition: 0.4s ease-out;
  position: relative;
  top: -175px;
  left: 0px;
  opacity: 0;
}

.text-img h3 {
  font-size: 1.4rem;
}

.text-img:not(:first-child) {
    margin-left: -40px;
}

.card:hover .text-img {
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  -webkit-transition: 0.4s ease-out;
  transition: 0.4s ease-out;
}

.card:hover ~ .text-img {
  opacity: 1;
  position: relative;
  left: 0px;
  -webkit-transition: 0.4s ease-out;
  transition: 0.4s ease-out;
}
input {
  margin: 1vw;
  padding: 10px 20px;
  font-size: 1.2rem;
}

.parent button {
  padding: 20px 40px;
  font-size: 2.4rem;
  border-radius: 8px;
}

.gameCardSmall {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 100px;
  margin: 1vw;
  padding: 1vw 0.5vw;
  overflow-y: scroll;
  background: linear-gradient(344deg, rgba(83, 28, 119, 0.644) 31%, rgba(232, 137, 235, 0.493) 100%);
  box-shadow: 2px 0px 10px rgba(0, 0, 0, 0.363);
}

.gameCardSmall h1 {
  font-size: 1.4rem;
  text-decoration: none;
  width: 100%;

}

.searchResults .description-section {
  top: 0;
}

.flex-column {
  justify-content: center;
  align-items: center;
}

.searchResults {
  width: 80vw;
  overflow-x: scroll;
  justify-content: flex-start;
}

.parent {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: repeat(6, minmax(auto, auto));
  grid-column-gap: 20px;
  grid-row-gap: 15px;
  position: relative;
  margin: auto;
  margin-top: 10px;
  overflow-y: scroll;
  width: 70vw;
  background: #F5F5F5;
  box-shadow: 5px 5px 15px rgba(rgb(53, 52, 52), .5);
  border-radius: 10px;
  color: rgb(160, 179, 195);
  padding: 20px;
  }
  
  .div1 { grid-area: 1 / 1 / 2 / 2; }
  .div2 { grid-area: 2 / 1 / 5 / 2; }
  .div3 { grid-area: 5 / 1 / 6 / 2; }
  .div4 { grid-area: 6 / 1 / 7 / 2; }
  .div5 { grid-area: 1 / 2 / 2 / 3; }
  .div6 { grid-area: 2 / 2 / 5 / 3; }
  .div7 { grid-area: 5 / 2 / 6 / 3; }
  .div8 { grid-area: 6 / 2 / 7 / 3; }
  .div9 { grid-area: 1 / 3 / 2 / 4; }
  .div10 { grid-area: 2 / 3 / 3 / 4; }
  .div11 { grid-area: 3 / 3 / 4 / 4; }
  .div12 { grid-area: 4 / 3 / 5 / 4; }
  .div13 { grid-area: 5 / 3 / 6 / 4; }
  .div14 { grid-area: 6 / 3 / 7 / 4; }

  .parent label {
    font-size: 1.6rem;
    font-weight: bold;
  }

  .parent textarea {
    height: 90%;
    width: 90%;
    font-size: 1.4rem;
    margin-top: 10px;
    padding: 10px;
    overflow-y: scroll;
    border-radius: 8px;
    border: 1px solid rgba(0, 0, 0, 0.1);
  }

  .parent input {
    height: 90%;
    width: 90%;
    font-size: 1.4rem;
    margin-top: 10px;
    border-radius: 8px;
    border: 1px solid rgba(0, 0, 0, 0.1);
  }

  .parent select {
    max-height: 60px;
    margin-top: 10px;
    width: 90%;
    font-size: 1.4rem;
    border-radius: 8px;
    border: 1px solid rgba(0, 0, 0, 0.1);
  }


/* center container in the middle */
.container-card {
  position: relative;
  margin: auto;
  margin-top: 50px;
  overflow: hidden;
  width: 70vw;
  height: 70vh;
  background: #F5F5F5;
  box-shadow: 5px 5px 15px rgba(126, 154, 186, 0.5);
  border-radius: 10px; }

.container-card p {
  font-size: 1.6em;
  color: #7e9aba;
  letter-spacing: 1px; }

.container-card h1 {
  font-size: 2.2em;
  color: #4e4e4e;
  margin: 5px 0;
  margin-bottom: 10px; }

.container-card h2 {
  color: #a0b3c3;
  font-size: 1.8em;
  margin: 5px 0; }

.container-card img {
  max-width: 40%;
  max-height: 50vh;
  margin-top: 47px; }

.container-card .images {
  margin: 0 50px; }

.container-card .product {
  position: absolute;
  width: 50%;
  height: 100%;
  top: 7%;
  left: 45%; }

.container-card .desc {
  text-transform: none;
  letter-spacing: 0;
  margin-bottom: 17px;
  color: #4e4e4e;
  font-size: 1.7em;
  line-height: 1.2em;
  margin-right: 25px;
  text-align: justify; }

.container-card button {
  padding: 10px;
  display: inline-block;
  outline: 0;
  border: 0;
  margin: -1px;
  border-radius: 2px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #F5F5F5;
  cursor: pointer; }
  .container-card button:hover {
    background: #7e9aba;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out; }

.add {
  background: #a9b7cd; }

.is-wish {
  background: #7e9aba; }

.is-blue {
  background: #a9b7cd; }

.container-card .add {
  width: 67%; }

.container-card .like {
  width: 22%; }

.container-card .sizes {
  display: grid;
  color: #aac6d9;
  grid-template-columns: repeat(auto-fill, 30px);
  width: 60%;
  grid-gap: 4px;
  margin-left: 50px;
  margin-top: 5px; }
  .container-card .sizes:hover {
    cursor: pointer; }

.container-card .pick {
  margin-top: 11px;
  margin-bottom: 0;
  margin-left: 50px; }

.container-card .size {
  padding: 9px;
  border: 1px solid #c9d2e0;
  font-size: 0.7em;
  text-align: center; }
  .container-card .size:hover {
    background: #7e9aba;
    color: #F5F5F5;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out; }

.container-card .focus {
  background: #7e9aba;
  color: #F5F5F5; }

.game-info {
  max-height: 60%;
  overflow-y: scroll;
  margin-top: 10px;
  margin-bottom: 20px; }

.size img {
  position: absolute;
  width: 0;
  bottom: 100px;
  outline: 10px solid #5482dd;
  box-shadow: 0 0 120px black;
  z-index: 1;
  margin-left: -20px;
  opacity: 0;
  -webkit-transition: width 0.2s ease-in;
  transition: width 0.2s ease-in; }

.size:hover img {
  opacity: 1;
  width: 100%;
  -webkit-transition: width 0.2s ease-in-out;
  transition: width 0.2s ease-in-out; }

#price {
  display: inline;
  background: #a0b3c3;
  color: #F5F5F5;
  padding: 5px 10px;
  border-radius: 8px; }

